import { Component }																from '@angular/core';
import { DomSanitizer }                 											from '@angular/platform-browser';
import { NavParams, LoadingController, MenuController }   							from 'ionic-angular';
import { C8oRouter }                    											from 'c8ocaf';
import { C8oPage }                      											from 'c8ocaf';
import { ActionBeans } 																from '../../services/actionbeans.service';
import {ChangeDetectorRef, ChangeDetectionStrategy, InjectionToken, Injector, Type} from "@angular/core";


/*
	You can customize your page class by writing code between the :

   		Begin_c8o_XXXX and
   		End_c8o_XXXX

   	Comments.

   	Any code placed outside these these comments will be lost when the application is generated
*/


/*Begin_c8o_PageImport*/
import { ToastController } from 'ionic-angular';
import { SettingsPage } from '../SettingsPage/settingspage'; 
/*End_c8o_PageImport*/

@Component({
  selector: 'page-pluspage',
  templateUrl: 'pluspage.html',
})
export class PlusPage extends C8oPage  {
	

	public actionBeans: ActionBeans;
	/*Begin_c8o_PageDeclaration*/
    toastCtrl: ToastController
	/*End_c8o_PageDeclaration*/

	constructor(routerProvider : C8oRouter, navParams: NavParams, loadingCtrl: LoadingController, sanitizer: DomSanitizer, ref: ChangeDetectorRef, injector: Injector, menuCtrl: MenuController){
		super(routerProvider, navParams, loadingCtrl, sanitizer, ref, injector, menuCtrl);
		this.actionBeans = this.getInstance(ActionBeans);
		
		
		/*Begin_c8o_PageConstructor*/
        this.toastCtrl = this.getInstance(ToastController);
		/*End_c8o_PageConstructor*/
		
    }
	
	/*Begin_c8o_PageFunction*/
	/*End_c8o_PageFunction*/
	
	

	/**
	 * Function openOauth
	 *   
	 * 
	 * @param event , the event received
	 */
	CTS1506008737136(event) {
	/*Begin_c8o_function:CTS1506008737136*/
        this.router.doOAuthLogin(
                'https://idp-dev.sncf.fr:443/openam/oauth2/IDP/authorize?'  +                       // Authorize URL
                    'client_id=sncf.id.sandbox.app' +                                                   // Client ID as registred in the app portal
                    '&response_type=id_token+token' +                                                   // We ask for implicit flow
                    '&scope=openid' +                                                                   // Scopes...
                    '&response_mode=fragment&state=12345&nonce=678910',                                 // Implicit flow
                'https://localhost/oidc-redirect_uri',                                               // the call back URL to check (As declared in the app portal)
                'SncfOauth.LoginWithToken',                                                      // The server sequence to be launched to check the access token
                'SncfOauth.checkAccessToken'                                                     // The sequence to execute to check access token
        ).then((response)=>{
            let toast = this.toastCtrl.create({
                message: "Le token gnr est: " + response.token,
                duration: 3000,
                position: 'bottom'
              });
            toast.present();
            this.c8o.log.info("SNCF TOKEN => " + JSON.stringify(response));
        });
	/*End_c8o_function:CTS1506008737136*/
	}

	/**
	 * Function CustomAction
	 *   
	 * 
	 * @param event , the event received
	 */
	CTS1505949096434(event) {
	/*Begin_c8o_function:CTS1505949096434*/
	    let toast = this.toastCtrl.create({
            message: "Cette page n'est pas implmente",
            duration: 3000,
            position: 'bottom'
          });
        toast.present();
	/*End_c8o_function:CTS1505949096434*/
	}

	/**
	 * Function CustomAction
	 *   
	 * 
	 * @param event , the event received
	 */
	CTS1505949295644(event) {
	/*Begin_c8o_function:CTS1505949295644*/
	    let toast = this.toastCtrl.create({
            message: "Cette page n'est pas implmente",
            duration: 3000,
            position: 'bottom'
          });
        toast.present();
	/*End_c8o_function:CTS1505949295644*/
	}

}



