import { Component, ViewChild}                              from '@angular/core';

import { Platform, Nav, App, LoadingController}             from 'ionic-angular';
import { StatusBar }                                        from '@ionic-native/status-bar';

// Convertigo CAF Imports
import { C8oRouter }                                        from 'c8ocaf';
import { C8oRoute, C8oRouteOptions, C8oRouteListener}       from 'c8ocaf'
import { C8oPage}                                           from "c8ocaf";
import { C8o, C8oSettings, C8oLogLevel,C8oProgress }        from "c8osdkangular";

/*
	You can customize your application class by writing code between the :

   		Begin_c8o_XXXX and
   		End_c8o_XXXX
   		
   	Comments.
   	
   	Any code placed outside these these comments will be lost when the application is generated
*/

/*Begin_c8o_AppImport*/
/*End_c8o_AppImport*/

import { TabsPage } from "../pages/TabsPage/tabspage";
import { NewsPage } from "../pages/NewsPage/newspage";
import { ProgressPage } from "../pages/ProgressPage/progresspage";
import { DetailsNewsPage } from "../pages/DetailsNewsPage/detailsnewspage";
import { DocumentsPage } from "../pages/DocumentsPage/documentspage";
import { AstucesPage } from "../pages/AstucesPage/astucespage";
import { SondagesPage } from "../pages/SondagesPage/sondagespage";
import { SettingsPage } from "../pages/SettingsPage/settingspage";
import { PlusPage } from "../pages/PlusPage/pluspage";
import { VideoPage } from "../pages/VideoPage/videopage";
 


/**
 * Disable comments to run in prod mode
 */
/*import {enableProdMode} from '@angular/core';
 enableProdMode();*/


@Component({
    templateUrl: 'app.html'
})
export class MyApp {
    @ViewChild(Nav) nav: Nav;
    rootPage = ProgressPage;
    pages : Array<{title: string, component: any, includedInAutoMenu?: boolean}>;
    pagesKeyValue: any;

    /*Begin_c8o_AppDeclaration*/
	/*End_c8o_AppDeclaration*/

    constructor(platform: Platform, statusBar: StatusBar, private c8o: C8o, private router: C8oRouter, private loader: LoadingController, private app: App) {
        /**
         * declaring page to show in Menu
         */
        this.pages = [ { title: "Title for TabsPage", component: TabsPage, includedInAutoMenu: false}, { title: "First page", component: NewsPage, includedInAutoMenu: false}, { title: "Title for ProgressPage", component: ProgressPage, includedInAutoMenu: true}, { title: "Title for DetailsNewsPage", component: DetailsNewsPage, includedInAutoMenu: false}, { title: "Title for DocumentsPage", component: DocumentsPage, includedInAutoMenu: true}, { title: "Title for AstucesPage", component: AstucesPage, includedInAutoMenu: true}, { title: "Title for AstucesPage1", component: SondagesPage, includedInAutoMenu: true}, { title: "Title for SettingsPage", component: SettingsPage, includedInAutoMenu: true}, { title: "Title for PlusPage", component: PlusPage, includedInAutoMenu: true}, { title: "Title for VideoPage", component: VideoPage, includedInAutoMenu: true}];
        this.pagesKeyValue = {TabsPage:TabsPage,NewsPage:NewsPage,ProgressPage:ProgressPage,DetailsNewsPage:DetailsNewsPage,DocumentsPage:DocumentsPage,AstucesPage:AstucesPage,SondagesPage:SondagesPage,SettingsPage:SettingsPage,PlusPage:PlusPage,VideoPage:VideoPage}
        this.router.pagesArray = this.pages;
        this.router.pagesKeyValue = this.pagesKeyValue;


        /* ============================================================================================================
           Convertigo Angular Framework (CAF) initialization...
           ============================================================================================================
         * Thanks to Convertigo CAF router we can manage call and navigation :
         *
         * Create a C8orouteOptions in order to define basic and repetitive routes options that will be used in C8oRoute
         * We can define actions such as beforeCall that allow us to run code before the C8o Call
         */

        let tableOptions = new C8oRouteOptions()
            .setBeforeCall(() => {
                //Do what ever has to be done...
            })
            .setAfterCall(()=>{
                //Do what ever has to be done...
            })
            .setDidEnter((page: C8oPage, c8o: C8o) => {
                c8o.log.trace("DidEnter was called from the new routing table and with page : " + page.constructor.name)
            })
            .setDidLeave((page: C8oPage, c8o: C8o) => {
                c8o.log.trace("DidLeave was called from the new routing table and with page : " + page.constructor.name)
            })
            .setTargetAnimate(true)
            .setTargetDuration(250);

        /**
         * The generated Routing Table
         */
         this.router.addRouteListener(new C8oRouteListener(["fs://monmob.get#getDetailsNews"]).addRoute(new C8oRoute((data:any)=>{return true}, tableOptions).setTarget("push", DetailsNewsPage)));
 
        

        /**
         *  Define a C8oSettings Object in order to declare settings to be used in the C8oInit method
         */
        let settings: C8oSettings = new C8oSettings();
        settings
            .setLogRemote(true)
            .setLogC8o(true)
            .setLogLevelLocal(C8oLogLevel.DEBUG)
            .setTimeout(120000);

        /**
         * Then we assign C8oSettings to our c8o Object with the init method
         */
        this.c8o.init(settings);

        
        /* ============================================================================================================
             End of Convertigo Angular Framework (CAF) initialization...
           ============================================================================================================*/
		   
		/*Begin_c8o_AppConstructor*/
		/*End_c8o_AppConstructor*/
		   
        platform.ready().then(() => {
            statusBar.backgroundColorByHexString('#009AA6');
            /**
             * Then we finalize initialization
             */
            this.c8o.finalizeInit().then(()=>{
                /*Begin_c8o_AppInitialization*/
            //set Prefix_url
            this.router.sharedObject = {prefix_site_clipper: "https://mb.convertigo.net/cems/projects/MonMobile/connector=SC,context=ctx.siteclipper/http/"}            
            //Testing connection Type
            let initialConnectionType = "Unknown";
            if(navigator["connection"] != undefined){
                initialConnectionType = navigator["connection"].type;
                if(initialConnectionType == undefined){
                    initialConnectionType = navigator["connection"].effectiveType;
                }
            }
            
            //if there is network logging and synchronizing
            if(initialConnectionType == "Unknown" || initialConnectionType != navigator["connection"].NONE){
                // fisrt call Logging
                //this.c8o.callJson("fs://monmob.reset").then((resp)=>{
                    this.c8o.callJson(".Login")
                    // handling response
                    .then((response: any, parameters: Object)=>{
                        this.c8o.log.info('login => logged');
                        this.c8o.log.info(JSON.stringify(response));
                        // second call sync
                        return this.c8o.callJson("fs://monmob.sync", "continuous", true)
                    })
                    // handling second response
                    .then((response: any, parameters: Object)=>{
                        this.c8o.log.info('Sync => Database Synchronized => set root page');
                        this.nav.setRoot(TabsPage, null, null, null);
                        return null;
                    })
                    // handling progress for sync
                    .progress((progress:any)=>{
                        this.c8o.log.info(progress.toString());
                        // if progress is finished then show another view
                        if(progress.finished){
                            this.c8o.log.info('Progress finished');
                        }
                    })
                    // handling errors
                    .fail((err:any, params: any)=>{
                        this.c8o.log.warn('Fail for initial sync =>\n Error: ' + JSON.stringify(err) + '\n Parameters:  ' + JSON.stringify(params));
                        this.nav.setRoot(TabsPage, null, null, null);
                    });
                   /* return null;
                });*/
               
            }
            //if no connection go to TabsPage
            else{
                this.nav.setRoot(TabsPage, null, null, null);
            }
            
            // Handling online network event
            document.addEventListener("online", () => {
                this.c8o.log.info('online Event Handle =>  Login and Sync process staring again');
                this.c8o.callJson(".Login")
                // handling response
                .then((response: any, parameters: Object)=>{
                    this.c8o.log.info('login => logged');
                    // second call sync
                    return this.c8o.callJson("fs://monmob.sync", "continuous", true)
                })
                // handling second response
                .then((response: any, parameters: Object)=>{
                    this.c8o.log.info('Sync => Database Synchronized');
                    return null;
                })
                // handling progress for sync
                .progress((progress:any)=>{
                    this.c8o.log.info(progress.toString());
                    // share progress status with another page (ProgressPage)
                    //this.router.storeResponseForView("Page", "fs://monmob.sync#progress", progress, null, null, null);
                    // if progress is finished then show another view
                    if(progress.finished){
                        this.c8o.log.info('Progress finished => SetRoot TabsPage');
                    }
                })
                // handling errors
                .fail((err:any, params: any)=>{
                    this.c8o.log.warn('Fail for initial sync =>\n Error: ' + JSON.stringify(err) + '\n Parameters:  ' + JSON.stringify(params));
                });
            });
			/*End_c8o_AppInitialization*/
            });
        });

    }

	getRootNav() {
		let rootNavs = this.app.getRootNavs();
		return rootNavs.length > 0 ? rootNavs[0]:null;
	}
	
    openPage(page) {
		let rootNav = this.getRootNav();
		if (rootNav) {
			rootNav.setRoot(page.component);
		}
    }
	
    openPageWithName(name) {
		let rootNav = this.getRootNav();
		if (rootNav) {
			rootNav.setRoot(name);
		}
    }
    
    getPagesIncludedInAutoMenu(){
        let arrayIncluded: Array<any> = [];
        for (let p of this.pages){
            if (p["includedInAutoMenu"]) {
                arrayIncluded.push(p);
            }
        }
        return arrayIncluded;
    }

	/*Begin_c8o_AppFunction*/
	/*End_c8o_AppFunction*/
}


